<template>
    <div class="SearchComp">
        <div class="searchBox">
            <el-row :gutter="10">
                <el-col :span="2">
                    <div class="searchName"></div>
                </el-col>
                <el-col :span="10">
                    <el-select size="mini" v-model="orderType" placeholder="" style="width:100%;">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="2">
                    <div class="searchName"></div>
                </el-col>
                <el-col :span="10">
                    <el-select size="mini" v-model="orderStatus" placeholder="" style="width:100%;">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
            </el-row>
            <el-row :gutter="10" style="margin-top:10px;">
                <el-col :span="2">
                    <div class="searchName"></div>
                </el-col>
                <el-col :span="10">
                    <el-date-picker size="mini" v-model="orderDateRange" type="daterange" range-separator="" start-placeholder="" end-placeholder="" style="width:100%;">
                    </el-date-picker>
                </el-col>
                <el-col :span="2">
                    <el-button @click="clearSearchData" size="mini" type="primary"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button @click="searchData" size="mini" type="primary"></el-button>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
let options = [{ value: '0', label: '' }, { value: '1', label: '' }, { value: '2', label: '' }];
let orderData = [
    {
        orderType: "",
        orderDate: "2017-12-12",
        orderCode: "langjiu12345678",
        goodsImg: "src/assets/goodsItem.png",
        orderDesc: "1 10 53  558m l*6)",
        "volume": 500,
        "strength": 53,
        "price": 1000,
        num:5,
        money:4000,
        orderStatus:''
    },
    {
        orderType: "",
        orderDate: "2017-12-12",
        orderCode: "langjiu12345678",
        goodsImg: "src/assets/goodsItem.png",
        orderDesc: "2 10 53  558m l*6)",
        "volume": 500,
        "strength": 53,
        "price": 1000,
        num:5,
        money:4000,
        orderStatus:''
    }
];
export default {
    name: 'SearchComp',
    props: ['searchUrl'],
    data() {
        return {
            orderType: '',
            orderStatus: '',
            orderDateRange: '',
            options: options
        }
    },
    methods: {
        searchData() {
            let startTime = '';
            let endTime = '';
            if (this.orderDateRange) {
                startTime = this.orderDateRange[0].getTime();
                endTime = this.orderDateRange[1].getTime();
            }
            let params = {
                orderType: this.orderType,
                orderStatus: this.orderStatus,
                startTime: startTime,
                endTime: endTime
            };
            this.$emit('searchData',orderData)

        },
        clearSearchData() {
            this.orderType = '';
            this.orderStatus = '';
            this.orderDateRange = '';
        }
    }
}
</script>
<style lang="scss" scoped>
@import './SearchComp.scss';
</style>





