<template>
    <div class="MsgItem">
        <div class="msgTitle">{{msgItem.msgTitle}}</div>
        <div class="msgDate">{{msgItem.msgDate}}</div>
        <div class="msgBrief">{{msgItem.msgBrief}}</div>
    </div>
</template>
<script>
export default {
    name: 'MsgItem',
    props: {
        msgItem: {
            type: Object,
            default: function() {
                return {
                    msgTitle: ' ',
                    msgDate: '2017-01-01',
                    msgBrief: '12 '
                }
            }
        }
    },
    data() {
        return {}
    }
}
</script>
<style lang="scss" scoped>
@import "./MsgItem.scss";
</style>




