<template>
    <div class="CostOff">
        <el-button @click="add" size="mini" type="primary"></el-button>
        <el-dialog :visible.sync="dialogVisible" width="1000" @close="handleClose" @open="handleOpen">
            <div class="CostOffTitle" slot="title">
                <h2 class="title"></h2>
            </div>
            <div class="dialogContainer">
                <el-row>
                    <el-col :span="3"></el-col>
                    <el-col :span="3">10000</el-col>
                    <el-col :span="4"></el-col>
                    <el-col :span="6">
                        <el-input v-model="useOffMoney" size="mini" placeholder=""></el-input>
                    </el-col>
                </el-row>
                <el-table :data="searchData" show-summary sum-text="" border style="width: 100%">
                    <el-table-column prop="moneyType" label=""></el-table-column>
                    <el-table-column prop="useableMoney" label=""></el-table-column>
                    <el-table-column prop="currentMoney" label=""></el-table-column>
                </el-table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false" size="mini"> </el-button>
                <el-button type="primary" @click="confirm" size="mini"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
let searchData = [
    {
        moneyType: 'E',
        useableMoney: 2000,
        currentMoney: 1000,
    },
    {
        moneyType: 'Q',
        useableMoney: 3000,
        currentMoney: 2000,
    },
    {
        moneyType: 'F',
        useableMoney: 4000,
        currentMoney: 3000,
    }
];
export default {
    name: 'AddNewGoods',
    props:["goodsData"],
    data() {
        return {
            searchUrl: '',
            dialogVisible: false,
            searchData: searchData,/*  */
            useOffMoney:'',/*  */
        }
    },
    methods: {
        add() {
            this.dialogVisible = true;
        },
        handleClose(done) {/*  */
            this.searchData = [];
        },
        handleOpen() {/*  */
            // this.searchTable();
            console.log(this.goodsData)
        },
        cancel() {
            this.dialogVisible = false;
        },
        confirm() {
            let _this = this;
            _this.dialogVisible = false;
            let eventPayload = {

            };
            _this.$emit('CostOffEvent', eventPayload);
        },

        searchTable() {/*  */
            let _this = this;
            let url = this.searchUrl;
            _this.$http.get(url, )
                .then(res => {
                    let data = res.data.data;
                    _this.searchData = data;
                });
        }
    },
    mounted() {
        
    }
}
</script>
<style lang="scss">
@import './CostOff.scss';
</style>




