<template>
    <div class="Home">
        <div class="bannerContainer">
            <div class="imgContainer">
                <img src="../../../assets/banner.png" alt="">
            </div>
            <div class="msgContainer">
                <div class="msg">
                    <div class="title"></div>
                    <ul>
                        <li v-for="(item,index) in msgArr" :key="index">{{item}}</li>
                    </ul>
