                <div class="wait">
                    <div class="title"></div>
                    <div class="waitItems">
                        <div class="row1">
                            <div class="left">
                                <div>
                                    <i class="el-icon-document"></i>
                                </div>
                                <div></div>
                            </div>
                            <div class="right">
                                <div>
                                    <i class="el-icon-document"></i>
                                </div>
                                <div></div>
                            </div>
                        </div>
                        <div class="row2">
                            <div class="left">
                                <div>
                                    <i class="el-icon-document"></i>
                                </div>
                                <div></div>
                            </div>
                            <div class="right">
                                <div>
                                    <i class="el-icon-document"></i>
                                </div>
                                <div></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapActions } from 'Vuex';
let msgArr = [
    'CALL ',
    ' ',
    '',
    '',
    '',
    ''
];
export default {
    name: 'Home',
    data() {
        return {
            msgArr: msgArr
        }
    },

    methods: {
        ...mapActions({
            fetchPurchaseCountAsync: 'goodsCenter/fetchPurchaseCountAsync'
        }),
        handleClick() { }
    },
    mounted(){
        this.fetchPurchaseCountAsync();
        this.$store.commit('changeCurrentNav', { hash: '/Home' });
    }
}
</script>
<style lang="scss" scoped>
@import "./Home.scss";
</style>




