<template>
    <div class="ApplyReturn">
        <div class="ApplyReturnTitle">
            <el-row>
                <div class="sendTitleTitle"></div>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="2">
                    <div class="name"></div>
                </el-col>
                <el-col :span="5">
                    <div class="desc">{{infoData.orderName}}</div>
                </el-col>
                <el-col :span="2">
                    <div class="name"></div>
                </el-col>
                <el-col :span="4">
                    <div class="desc">{{infoData.orderCode}}</div>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                
                <el-col :span="2">
                    <div class="name"></div>
                </el-col>
                <el-col :span="5">
                    <div class="desc">{{infoData.orderCode}}</div>
                </el-col>
                <el-col :span="2">
                    <div class="name"></div>
                </el-col>
                <el-col :span="7">
                    <div class="desc">{{infoData.address}}</div>
                </el-col>
            </el-row>

        </div>
        <div class="ApplyReturnTable">
            <el-table :data="infoData.goodsList" style="width: 100%">
                <el-table-column prop="" label="" width="400">
                    <template slot-scope="scope">
                        <div class="detailContainer">
                            <div :style='{"backgroundImage":`url(${scope.row.goodsImg})`}' class="goodsImg"></div>
                            <div class="desc">{{scope.row.brief}}</div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="volume" label="">
                    <template slot-scope="scope">
                        <p>: {{ scope.row.volume }}ml</p>
                        <p>: {{ scope.row.strength }}</p>
                    </template>
                </el-table-column>
                <el-table-column prop="orderTotalNum" label=""></el-table-column>
                <el-table-column prop="diliverNum" label=""></el-table-column>
                <el-table-column prop="applyNum" label=""></el-table-column>
                <el-table-column prop="applySendNum" label=""></el-table-column>
            </el-table>
        </div>
        <div class="ApplyReturnFooter">
            <el-row>
                <el-col :span="16">
                    <div class="footerLeft">1</div>
                </el-col>
                <el-col :span="4">
                    <div class="price">
                        <span class="name"></span>
                        <span class="money">{{totalMoney}}</span>
                    </div>
                </el-col>
                <el-col :span="4">
                    <div class="submitBtn">
                        <el-button size="mini" type="primary"></el-button>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
let infoData = {
    orderName: '',
    orderCode: 'langjiu12345678',
    carrayMethod: '',
    expectDate: '2017-01-01',
    address: '1980',
    goodsList: [
        {
            goodsImg: "src/assets/goodsItem.png",
            "brief": "110 53500500500500",
            orderTotalNum: 1000,
            diliverNum: 2000,
            restDiliverNum: 3000,
            volume: 500,
            strength: 50,
            applyNum: 101,
            applySendNum: 201.00,
            price: 10,
        },
        {
            goodsImg: "src/assets/goodsItem.png",
            "brief": "210 53500500500500",
            orderTotalNum: 1000,
            diliverNum: 2000,
            restDiliverNum: 3000,
            volume: 500,
            strength: 50,
            applyNum: 101,
            applySendNum: 201.00,
            price: 11,
        },
        {
            goodsImg: "src/assets/goodsItem.png",
            "brief": "310 53500500500500",
            orderTotalNum: 1000,
            diliverNum: 2000,
            restDiliverNum: 3000,
            volume: 500,
            strength: 50,
            applyNum: 101,
            applySendNum: 201.00,
            price: 12,
        }
    ]
};
export default {
    name: 'ApplyReturn',
    props: {
        infoData: {
            default() {
                return infoData
            }
        }
    },
    computed: {
        totalMoney() {
            let total = 0;
            this.infoData.goodsList.forEach(v => {
                total += (v.applyNum * v.price);
            });
            return total;
        }
    }
}
</script>
<style lang="scss" scoped>
@import './ApplyReturn.scss';
</style>



